// src/components/home/ImagesToggleButton.tsx
import React from "react";
import { Button, CircularProgress } from "@mui/material";
import ImageIcon from "@mui/icons-material/Image";

interface Props {
  count: number;
  loading: boolean;
  open: boolean;
  onToggle: () => void;
}

export default function ImagesToggleButton({ count, loading, open, onToggle }: Props) {
  return (
    <Button
      variant={open ? "contained" : "outlined"}
      startIcon={loading ? <CircularProgress size={16} /> : <ImageIcon />}
      onClick={onToggle}
      sx={{ borderRadius: 2 }}
    >
      {loading ? "Loading..." : `${count} Image${count !== 1 ? "s" : ""}`}
    </Button>
  );
}