// src/components/home/InspectionList.tsx
import React from "react";
import { Inspection } from "../services/AddonReports";
import InspectionCard from "./InspectionCard";

interface Props {
  inspections: Inspection[];
  onSelect: (insp: Inspection) => void;
  loading: boolean;
}

export default function InspectionList({ inspections, onSelect, loading }: Props) {
  if (loading) return <p>Loading reports...</p>;
  if (inspections.length === 0) return <p>No inspections found.</p>;

  return (
    <>
      {inspections.map((insp) => (
        <InspectionCard key={insp.id} inspection={insp} onEdit={() => onSelect(insp)} />
      ))}
    </>
  );
}