// src/components/home/ReportImagesGallery.tsx
import React from "react";
import { Box, Typography, IconButton, CircularProgress } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

interface Props {
  images: string[];
  loading: boolean;
  onInsertImage: (url: string, index: number) => void;
}

export default function ReportImagesGallery({ images, loading, onInsertImage }: Props) {
  if (loading) return <CircularProgress />;
  if (images.length === 0) return <Typography color="gray">No images found</Typography>;

  return (
    <Box sx={{
      display: "grid",
      gridTemplateColumns: "repeat(auto-fill, minmax(180px, 1fr))",
      gap: 2,
      mt: 2
    }}>
      {images.map((url, idx) => (
        <Box key={idx} sx={{ position: "relative", borderRadius: 2, overflow: "hidden", boxShadow: 3 }}>
          <img
            src={url}
            alt={`Image ${idx + 1}`}
            style={{ width: "100%", height: "auto", maxHeight: 220, objectFit: "cover" }}
            loading="lazy"
          />
          <IconButton
            onClick={() => onInsertImage(url, idx)}
            sx={{
              position: "absolute", top: 8, right: 8,
              bgcolor: "rgba(255,255,255,0.9)", "&:hover": { bgcolor: "#fff" }
            }}
          >
            <AddIcon />
          </IconButton>
        </Box>
      ))}
    </Box>
  );
}