// src/pages/Home.tsx

import React, { useEffect, useState } from "react";
import { Box, Button, CircularProgress } from "@mui/material";
import { fetchAddonReports, Inspection } from "../services/AddonReports";
import Loadingoverlay from "../common/LoadingOverlay";
import AlertSnackbar from "../common/AlertSnackbar";
import InspectionList from "../home/InspectionList";
import ReportModal from "../home/ReportModal";
import AppHeader from "../Header/AppHeader";
import { ColorLensOutlined } from "@mui/icons-material";
// import InspectionList from "../components/home/InspectionList";
// import ReportModal from "../components/home/ReportModal";
// import Loadingoverlay from "../components/common/Loadingoverlay";
// import AlertSnackbar from "../components/common/AlertSnackbar";
// import AppHeader from "../components/AppHeader";

export default function Home() {
  const [inspections, setInspections] = useState<Inspection[]>([]);
  const [selected, setSelected] = useState<Inspection | null>(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
const [searchQuery, setSearchQuery] = useState("");

  // Images state
  const [reportImages, setReportImages] = useState<string[]>([]);
  const [imagesLoading, setImagesLoading] = useState(false);
  const [showImages, setShowImages] = useState(false);


  const loadReports = async () => {
    setLoading(true);
    try {
      const data = await fetchAddonReports();
      setInspections(data);
      setMessage("Reports loaded!");
    } catch (err) {
      setError("Failed to load reports");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => { loadReports(); }, []);

  const openReport = (insp: Inspection) => {
    setSelected(insp);
    setReportImages([]);
    setShowImages(false);
    fetchReportImages(insp.id);
  };




  const flattenInspection = (obj: any, prefix = ""): Record<string, any> => {
    let result: Record<string, any> = {};
    for (const [key, value] of Object.entries(obj || {})) {
      const fullKey = prefix ? `${prefix}.${key}` : key;
      if (typeof value === "object" && value !== null) {
        result = { ...result, ...flattenInspection(value, fullKey) };
      } else {
        result[fullKey] = value;
      }
    }
    return result;
  };


  function formatKeyLabel(key: string): string {
    // Remove known prefixes like attr. or custom_attr.
    key = key.replace(/^(attr\.|custom_attr\.)/, "");

    const parts = key.split(".");
    let label = parts[parts.length - 1];

    // Replace underscores/dashes with spaces
    label = label.replace(/[_\-]/g, " ");

    // Insert a space before capital letters (for camelCase or PascalCase)
    label = label.replace(/([a-z])([A-Z])/g, "$1 $2");

    // Capitalize each word
    label = label.replace(/\b\w/g, (c) => c.toUpperCase());

    return label.trim();
  }

  const insertFieldAtSelection = async (key: string, value: string) => {
    try {
      await Word.run(async (context) => {
        let range = context.document.getSelection();

        //  Handle image URLs
        if (
          /^https?:\/\/.*\.(png|jpg|jpeg|gif|bmp|webp)$/i.test(value) ||
          value.includes("supabase.co/storage/")
        ) {
          const response = await fetch(value);
          const blob = await response.blob();
          const base64 = await blobToBase64(blob);

          range.insertInlinePictureFromBase64(base64, "Replace");
          await context.sync();

          setMessage(`Inserted image from ${key}`);
          return;
        }

        //  Handle text content
        const paragraphs = value
          .replace(/\r/g, "")
          .split(/\n{2,}/)
          .filter((p) => p.trim() !== "");

        // Clear selection before inserting
        range.insertText("", "Replace");

        for (let i = 0; i < paragraphs.length; i++) {
          const para = paragraphs[i].trim();

          if (/^[-]\s/.test(para)) {
            // bullet list
            const lines = para
              .split(/\n|(?=-\s)/)
              .map((line) => line.replace(/^[-]\s*/, "").trim())
              .filter((line) => line.length > 0);

            for (const line of lines) {
              const p = range.insertParagraph("", "After");
              insertFormattedText(p, line);
              p.style = "List Paragraph";
              range = p.getRange("End");
            }
          } else {
            const p = range.insertParagraph("", "After");
            insertFormattedText(p, para);
            range = p.getRange("End");
          }

          if (i < paragraphs.length - 1) {
            range.insertParagraph("", "After");
          }
        }

        await context.sync();
      });

      setMessage(`Inserted "${key}" value into document`);
    } catch (err) {
      console.error(err);
      setError(" Failed to insert field into Word document");
    }
  };

  // ---------------------------
  // Helper: convert Blob  Base64
  // ---------------------------
  function blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        const base64data = (reader.result as string).split(",")[1];
        resolve(base64data);
      };
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  }
/// insert formatted text with bold support
 function insertFormattedText(paragraph: Word.Paragraph, text: string) {
    const boldRegex = /\*\*(.*?)\*\*/g;
    let match;
    let lastIndex = 0;

    while ((match = boldRegex.exec(text)) !== null) {
      const beforeText = text.substring(lastIndex, match.index);
      if (beforeText) paragraph.insertText(beforeText, "End");

      const boldText = match[1];
      const boldRange = paragraph.insertText(boldText, "End");
      boldRange.font.bold = true;

      lastIndex = match.index + match[0].length;
    }

    const remaining = text.substring(lastIndex);
    if (remaining) paragraph.insertText(remaining, "End");
  }


  /// fetch images for a report
  const fetchReportImages = async (reportId: string) => { 
 setImagesLoading(true);
    try {
      const token = localStorage.getItem("token");
      const refreshToken = localStorage.getItem("refresh_token");

      if (!token) {
        setError("No authentication token found");
        return;
      }

      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      myHeaders.append("Authorization", `Bearer ${token}`);
      if (refreshToken) myHeaders.append("x-refresh-token", refreshToken);

      const raw = JSON.stringify({ reportId });

      const requestOptions: RequestInit = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow"
      };

      const response = await fetch("https://app.right-report.com/api/addon-report/images", requestOptions);

      if (!response.ok) throw new Error(`HTTP ${response.status}`);

      const result = await response.json();
      const imageUrls = result.files?.map((file: any) => file.publicUrl) || [];

      setReportImages(imageUrls);
      setShowImages(true); // auto-open images tab when loaded
      setMessage(`Loaded ${imageUrls.length} image(s)`);
    } catch (error) {
      console.error("Error fetching images:", error);
      setError("Failed to load images for this report");
      setReportImages([]);
    } finally {
      setImagesLoading(false);
    }
  };





 const filteredInspections = inspections.filter((insp) =>
    insp.attr?.no?.toString().toLowerCase().includes(searchQuery.toLowerCase())
  );
   

  return (
    <Box display="flex" flexDirection="column" height="100vh" bgcolor="#f4f6f8">
  <AppHeader onSearch={(query) => setSearchQuery(query)} />

      <Loadingoverlay open={loading} />
      <AlertSnackbar message={message} error={error} onClose={() => { setMessage(null); setError(null); }} />

      <Box display="flex" flexGrow={1}>
        <Box width={380} bgcolor="white" p={3} borderRight="1px solid #ddd">
          {/* <InspectionList inspections={inspections} onSelect={openReport} loading={loading} /> */}

          <InspectionList
  inspections={filteredInspections}
  onSelect={openReport}
  loading={loading}
/>

          <Button fullWidth variant="contained" onClick={loadReports} sx={{ mt: 2 }}>
            Refresh
          </Button>
        </Box>

        <ReportModal
          open={!!selected}
          inspection={selected}
          images={reportImages}
          imagesLoading={imagesLoading}
          showImages={showImages}
          onClose={() => setSelected(null)}
          onToggleImages={() => setShowImages(!showImages)}
        //   onInsertField={insertField}
          onInsertField={insertFieldAtSelection}   
        />
      </Box>

      
    </Box>
  );
}