// src/components/home/ReportModal.tsx
import React, { useState } from "react";
import {
    Modal,
    Paper,
    IconButton,
    Typography,
    Divider,
    Box,
    Button,
    TextField,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import AddIcon from "@mui/icons-material/Add";
import ReportFieldsList from "./ReportFieldsList";
import ImagesToggleButton from "./ImagesToggleButton";
import ReportImagesGallery from "./ReportImagesGallery";

interface Props {
    open: boolean;
    inspection: any;                    //  This is the selected report
    images: string[];
    imagesLoading: boolean;
    showImages: boolean;
    onClose: () => void;
    onToggleImages: () => void;
    onInsertField: (key: string, value: string) => void;
}

export default function ReportModal({
    open,
    inspection,
    images,
    imagesLoading,
    showImages,
    onClose,
    onToggleImages,
    onInsertField,
}: Props) {
    const [expandedFields, setExpandedFields] = useState<Record<string, boolean>>({});
    /// custom field sats
    const [customFields, setCustomFields] = useState<Record<string, string>>({});
    const [showCustomForm, setShowCustomForm] = useState(false);
    const [newFieldName, setNewFieldName] = useState("");
    const [newFieldValue, setNewFieldValue] = useState("")

    //  Add this guard in case modal opens before data loads
    if (!inspection) return null;

    return (

        <>
            <Modal
                open={open}
                onClose={onClose}
                sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                }}
            >
                <Paper
                    sx={{
                        position: "relative",
                        width: { xs: "95%", sm: "90%", md: 800 },
                        maxHeight: "90vh",
                        overflow: "auto",
                        p: 3,
                       
                        bgcolor: "#f9fbff",
                        boxShadow: 24,
                    }}
                >
                    <IconButton
                        onClick={onClose}
                        sx={{ position: "absolute", top: 12, right: 12, color: "grey.600" }}
                    >
                        <CloseIcon />
                    </IconButton>

                    <Typography variant="h6" fontWeight={700} color="#1e3a8a" gutterBottom>
                        Full Report  #{inspection.attr?.no || inspection.id}
                    </Typography>
                    <Divider sx={{ mb: 3 }} />

                    {/* Images Toggle Button */}
                    {/* <ImagesToggleButton
          count={images.length}
          loading={imagesLoading}
          open={showImages}
          onToggle={onToggleImages}
        /> */}
                    <Box sx={{ display: "flex",flexDirection: "column", gap: 2, mt: 2, mb: 2 }}>
                        <ImagesToggleButton
                            count={images.length}
                            loading={imagesLoading}
                            open={showImages}
                            onToggle={onToggleImages}
                        />

                    </Box>
                   

                    {/* Fields List */}
                    <Box mt={showImages ? 4 : 2}>
                       <ReportFieldsList
  inspection={inspection}           //  already correct
  onInsertField={onInsertField}
/>
                    </Box>

                     {/* Images Gallery */}
                    {showImages && (
                        <Box mt={3}>
                            <Typography variant="subtitle1" fontWeight={600} mb={2}>
                                Report Images
                            </Typography>


                            <ReportImagesGallery
                                images={images}
                                loading={imagesLoading}
                                onInsertImage={(url, idx) => onInsertField(`image_${idx + 1}`, url)}
                            />
                        </Box>
                    )}
                </Paper>
            </Modal>
          
        </>


    );
}