// src/components/home/InspectionCard.tsx

import React from "react";
import {
  Card,
  CardContent,
  Box,
  Typography,
  Chip,
  Button,
  Divider,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import { RemoveRedEyeOutlined } from "@mui/icons-material";

interface Props {
  inspection: any;
  onEdit: () => void;
}

export default function InspectionCard({ inspection, onEdit }: Props) {
  const a = inspection.attr || {};

  const formatted = inspection.formatedValues || {};
  const updated = inspection.updated || "";
  const isComplete = a.status === "complete";

  return (
   <Card
  sx={{
    mb: 2,
    borderRadius: 2,
    border: "1px solid #e2e8f0",
    boxShadow: "0 2px 8px rgba(0,0,0,0.06)",
    transition: "all 0.25s ease",
    overflow: "hidden",
    "&:hover": {
      boxShadow: "0 8px 20px rgba(0,0,0,0.12)",
      transform: "translateY(-3px)",
      borderColor: "#3b82f6",
    },
  }}
>
  <CardContent sx={{ p: 2 }}>
    {/* Top Header */}
    <Box display="flex" justifyContent="space-between" alignItems="flex-start" mb={1.5}>
      <Box>
        <Typography
          variant="h6"
          fontWeight={700}
          color="#1e293b"
          letterSpacing="-0.3px"
          fontSize="1.05rem"
        >
          #{a.no || ""}
        </Typography>

        <Box display="flex" alignItems="center" gap={0.5} mt={0.5}>
          <LocationOnIcon fontSize="small" sx={{ color: "#64748b", fontSize: "1rem" }} />
          <Typography variant="body2" color="text.primary" fontWeight={500} fontSize="0.82rem">
            {a.location || "Unknown Location"}
          </Typography>
        </Box>

        <Typography
          variant="body2"
          color="text.secondary"
          fontWeight={500}
          mt={0.3}
          fontSize="0.78rem"
        >
          {a.plan || a.genre }
        </Typography>
      </Box>

      {/* Status Badge */}
      <Chip
        label={isComplete ? "Complete" : "Pending"}
        size="small"
        sx={{
          bgcolor: isComplete ? "#dcfce7" : "#fffbeb",
          color: isComplete ? "#166534" : "#92400e",
          fontWeight: 700,
          fontSize: "0.72rem",
          px: 0.5,
          height: 30,
          borderRadius: 2,
        }}
      />
    </Box>

    <Divider sx={{ my: 1.5, borderColor: "#e2e8f0" }} />

    {/* Details Grid */}
    <Box
      sx={{
        display: "grid",
        gridTemplateColumns: { xs: "1fr", sm: "1fr 1fr" },
        gap: 1.5,
      }}
    >
     <Box>
            <Typography variant="caption" color="text.secondary" fontWeight={600} fontSize="0.72rem">
              Supplier / Sub-Supplier
            </Typography>
            <Typography variant="body2" fontWeight={700} color="#1e293b" mt={0.3} fontSize="0.85rem">
              {formatted.supplier || ""}
            </Typography>
          </Box>
<Box>
            <Typography variant="caption" color="text.secondary" fontWeight={600} fontSize="0.72rem">
              Client Name
            </Typography>
            <Typography variant="body2" fontWeight={700} color="#1e293b" mt={0.3} fontSize="0.85rem">
              {formatted.customer || ""}
            </Typography>
          </Box>
      <Box>
        <Typography variant="caption" color="text.secondary" fontWeight={600} fontSize="0.72rem">
          Inspector
        </Typography>
        <Typography fontWeight={700} color="#3b82f6" mt={0.3} fontSize="0.85rem">
          {a.inspector || "Unknown Inspector"}
        </Typography>
      </Box>

      <Box>
        <Typography variant="caption" color="text.secondary" fontWeight={600} fontSize="0.72rem">
          Created Date
        </Typography>
        <Typography fontWeight={600} color="#1e293b" mt={0.3} fontSize="0.85rem">
          {a.date
            ? new Date(a.date).toLocaleDateString("en-GB", {
                day: "numeric",
                month: "long",
                year: "numeric",
              })
            : ""}
        </Typography>
      </Box>
    </Box>

    {/* Footer */}
    <Box display="flex" justifyContent="space-between" alignItems="center" mt={2}>
      <Box display="flex" alignItems="center" gap={1}>
       <Box
              sx={{
                width: 9,
                height: 9,
                bgcolor: "#22c55e",
                borderRadius: "50%",
                boxShadow: "0 0 8px rgba(34,197,94,0.45)",
              }}
            />
            <Typography variant="body2" color="#166534" fontWeight={600} fontSize="0.75rem">
              Last update: {updated}
            </Typography>
          </Box>

      {/* Compact View Button */}
      <Button
        variant="contained"
        size="small"
        startIcon={<RemoveRedEyeOutlined />}
        onClick={onEdit}
        sx={{
          bgcolor: "#3b82f6",
          color: "white",
          fontWeight: 500,
          px: 1.5,
          py: 0.3,
          fontSize: "0.65rem",
          minHeight: "26px",
          borderRadius: 2,
          boxShadow: "0 1px 4px rgba(59,130,246,0.25)",
          "& .MuiButton-startIcon": {
            marginRight: 0.3,
            "& svg": {
              fontSize: "14px",
            },
          },
          "&:hover": {
            bgcolor: "#2563eb",
            boxShadow: "0 3px 10px rgba(59,130,246,0.35)",
          },
        }}
      >
        View
      </Button>
    </Box>
  </CardContent>
</Card>

  );
}