// src/taskpane/components/home/ReportFieldsList.tsx
import React from "react";
import { Box, Typography, IconButton, Button } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

interface ReportFieldsListProps {
  inspection: any;
  onInsertField: (key: string, value: string) => void;
}

const formatBeautifulDate = (dateString: string): string => {
  if (!dateString) return "Not Found";
  const date = new Date(dateString);
  if (isNaN(date.getTime())) return "Not Found";

  return date.toLocaleDateString("en-GB", {
    day: "numeric",
    month: "long",
    year: "numeric",
  });
};

const REQUIRED_FIELDS = [
  {
    label: "Inspector Name",
    key: "inspectorName",
    getValue: (d: any) =>
      d?.attr?.inspector 
  },
  {
    label: "Client Name",
    key: "clientName",
    // Priority: formatedValues > custom_attr > fallback
    getValue: (d: any) =>
      d?.formatedValues?.customer ||
      d?.custom_attr?.client ||
      d?.attr?.client ||
      "Not Found",
  },
  {
    label: "Supplier/Sub-Supplier Name",
    key: "supplierName",
    // Priority: formatedValues > custom_attr > attr.supplier (ID) fallback
    getValue: (d: any) =>
      d?.formatedValues?.supplier ||
      d?.custom_attr?.["Supplier Name"] ||
      d?.custom_attr?.supplier ||
      d?.attr?.supplier_name ||   // in case some reports use this
      (typeof d?.attr?.supplier === "string" ? d.attr.supplier : "Not Found"),
  },
  {
    label: "Inspection Location",
    key: "inspectionLocation",
    getValue: (d: any) => d?.attr?.location || "Not Found",
  },
  {
    label: "Date of Inspection",
    key: "inspectionDate",
getValue: (d: any) => formatBeautifulDate(d?.created_at),
  },
  {
    label: "Material Description",
    key: "materialDescription",
    getValue: (d: any) =>
      d?.attr?.material || d?.custom_attr?.["Material Description"] || "Not Found",
  },
  {
    label: "Inspection Test Plan Activity",
    key: "testPlanActivity",
    getValue: (d: any) => d?.attr?.plan || d?.ai_notes?.activity || "Not Found",
  },
  {
    label: "Inspector Observations",
    key: "inspectorObservations",
    getValue: (d: any) =>
      
      d?.ai_notes?.inspectorObservations ||
      "Not Found",
  },
  {
    label: "Non-Conformances",
    key: "nonConformances",
    getValue: (d: any) =>
      d?.ai_notes?.nonConformances ||
      d?.notes?.nonConformances ||
      d?.ai_notes?.["Non-Conformances"] ||
      "Not Found",
  },
];

export default function ReportFieldsList({
  inspection,
  onInsertField,
}: ReportFieldsListProps) {
  if (!inspection) return null;

  return (
    <>
      {REQUIRED_FIELDS.map(({ label, key, getValue }) => {
        const rawValue = getValue(inspection);
        const value = String(rawValue === "Not Found" ? rawValue : rawValue || "Not Found");
        const isLong = value.length > 200 && value !== "Not Found";
        const [expanded, setExpanded] = React.useState(false);
        const displayValue = expanded || !isLong ? value : `${value.substring(0, 200)}...`;

        // Detect image URLs inside text (for observations, etc.)
        const imageUrls = value.match(/https?:\/\/[^\s]+\.(png|jpg|jpeg|gif|webp)/gi) || [];
        const hasImages = imageUrls.length > 0;

        return (
          <Box
            key={key}
            display="flex"
            alignItems="flex-start"
            justifyContent="space-between"
            bgcolor="#ffffff"
            border="1px solid #e3eaf7"
            borderRadius={2}
            px={1.8}
            py={1.8}
            mb={1.5}
            boxShadow="0 1px 3px rgba(0,0,0,0.06)"
            sx={{
              transition: "all 0.2s ease",
              "&:hover": {
                boxShadow: "0 3px 8px rgba(0,0,0,0.1)",
                borderColor: "#0078d4",
              },
            }}
          >
            <Box flex={1} mr={2}>
              <Typography variant="subtitle2" fontWeight={600} sx={{ color: "#1e3a8a", mb: 0.5 }}>
                {label}
              </Typography>

              {/* If it's a pure image URL */}
              {/^https?:\/\/.*\.(png|jpg|jpeg|gif|webp)$/i.test(value) ? (
                <Box mt={1}>
                  <img
                    src={value}
                    alt={label}
                    style={{
                      maxWidth: "100%",
                      maxHeight: "220px",
                      borderRadius: "8px",
                      border: "1px solid #e0e6f1",
                      objectFit: "contain",
                    }}
                    loading="lazy"
                  />
                </Box>
              ) : (
                <>
                  <Typography
                    variant="body2"
                    color={value === "Not Found" ? "error" : "text.secondary"}
                    sx={{
                      whiteSpace: "pre-wrap",
                      wordBreak: "break-word",
                      lineHeight: 1.6,
                    }}
                  >
                    {displayValue}
                  </Typography>

                  {/* Show images inside long text when expanded */}
                  {hasImages && expanded && (
                    <Box mt={2} sx={{ display: "flex", flexWrap: "wrap", gap: 2 }}>
                      {imageUrls.map((url, i) => (
                        <img
                          key={i}
                          src={url.trim()}
                          alt={`Inline ${i + 1}`}
                          style={{
                            maxWidth: "100%",
                            maxHeight: 200,
                            borderRadius: 8,
                            border: "1px solid #ddd",
                          }}
                          loading="lazy"
                        />
                      ))}
                    </Box>
                  )}
                </>
              )}

              {isLong && (
                <Button
                  size="small"
                  onClick={() => setExpanded(!expanded)}
                  sx={{ mt: 1, textTransform: "none", color: "#0078d4", fontWeight: 500 }}
                >
                  {expanded ? "Show less" : "Show more"}
                </Button>
              )}
            </Box>

            <IconButton
              color="primary"
              onClick={() => onInsertField(label, value)}
              title="Insert into Word"
              sx={{
                bgcolor: "#eef4ff",
                "&:hover": { bgcolor: "#d9e8ff" },
                alignSelf: "flex-start",
              }}
            >
              <AddIcon />
            </IconButton>
          </Box>
        );
      })}
    </>
  );
}