import React from "react";
import { Box, CircularProgress, Backdrop } from "@mui/material";

interface LoadingOverlayProps {
  open: boolean;
  size?: number;
  message?: string;
}

export default function LoadingOverlay({
  open,
  size = 70,
  message = "Loading...",
}: LoadingOverlayProps) {
  return (
    <Backdrop
      open={open}
      sx={{
        zIndex: (theme) => theme.zIndex.drawer + 9999,
        color: "#fff",
        bgcolor: "rgba(255, 255, 255, 0.85)",
        flexDirection: "column",
        gap: 2,
      }}
    >
      <CircularProgress size={size} thickness={4.5} color="primary" />
      <Box sx={{ fontSize: "1.1rem", fontWeight: 500 }}>{message}</Box>
    </Backdrop>
  );
}